
.Slider{  
    width: 100vw;
    position: relative;
    overflow: hidden;
    img {
        width: 100%;
        object-fit: cover;
    }

}

.slick-dots{
    bottom: 20px;
    li button{
        width: 2em;
        height: 2em;
        border:1px solid #fff;
        border-radius: 50%;
        &:before{
            position: absolute;
            top: 29%;
            left: 29%;
            width: 1em;
            height: 1em;
            border-radius: 50%;
            content: "";
            opacity: .5;
            background-color: #fff;
            transform: translate(-50%,-50%);
            transition: all 0.3s ease-in-out ;
        }
        &:hover button{
            border-color: tomato; 
            &::before{
                background-color: tomato;
                opacity: 1;
            }
        }
    }
    li.slick-active button{
        border-color: tomato; 
        &:before{
            background-color: tomato;
            opacity: 1;
        }
    }
}


.slick-prev,.slick-next{
    width:150px;
    height: 300px;
    /* background-color:  red; */
    position: absolute;
    top: 20%;
    transform: translateX(-50%);
    z-index: 10;
   

}
.slick-prev:before,.slick-next:before{
    color: #000;
    font-size: 1.5rem;

}
.slick-prev:hover,.slick-next:hover,.slick-prev:focus,.slick-next:focus{
    outline: none;
}
.slick-next{
    right: -175px;
}
.slick-prev{
    left: 100px;
}



@media screen and (max-width:576px) {

    .slick-dots{
    bottom: 10px;
    }
    .slick-prev,.slick-next{
        width:120px;
        height: 150px;
  
    }
    .slick-next{
        right: -150px;
    }
    .slick-prev{
        left: 75px;
    }
    .slick-prev:before,.slick-next:before{
        font-size: 0.7rem;
    
    }
}
@media screen and (max-width:796px) {

    .slick-prev,.slick-next{
        top: 0;
    
    }
    .slick-prev:before,.slick-next:before{
        font-size: 1rem;
    
    }
    
}