.cart {
    background-color: #f1f3f6;
    padding: 100px 5vw;

    .cart-title {
        height: 70px;
        background-color: white;
        border-radius: 3px;
        text-align: center;
        width: 90vw;
        line-height: 70px;
        z-index: 2;
    }

    .cart-wrapper {
        display: flex;
        gap: 20px;

        .cart-items-wrapper {
            width: 60vw;
            margin-top: 20px;

            .cart-items {
                height: 200px;
                background-color: white;
                padding: 10px 30px;
                display: flex;
                align-items: center;
                gap: 30px;
                margin-top: 20px;

                div:first-child {
                    justify-content: center;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                }

                img {
                    width: 100px;
                    height: 115px;
                }

                .plus-minus {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    padding: 10px;
                    gap: 10px;

                    p {
                        border: 1px solid lightgray;
                        padding: 5px 15px;
                        border-radius: 2px;
                    }

                    button {
                        all: unset;
                        border: 1px solid lightgray;
                        border-radius: 50%;
                        height: 30px;
                        width: 30px;
                        position: relative;
                        cursor: pointer;

                        span {
                            position: absolute;
                            transform: translate(-50%, -50%);
                            left: 50%;
                            top: 40%;
                        }
                    }

                }

                div:last-child {
                    button {
                        color: blue;
                        cursor: pointer;

                        &:hover {
                            color: orange;
                        }
                    }
                }
            }

        }
    }

    .place-order {
        height: 70px;
        width: 60vw;
        background-color: white;
        display: flex;
        padding-right: 30px;
        cursor: pointer;
        position: sticky;
        bottom: 0px;
        box-shadow: 0px -13px 15px -1px rgba(0, 0, 0, 0.1);
        align-items: center;

        button {
            background-color: orange;
            color: white;
            width: 250px;
            height: 50px;
            text-align: center;
            margin-left: auto;

            &:hover {
                background-color: yellowgreen;
            }
        }

    }

    .saved {
        margin-top: 20px;
    }

    .summary {
        background-color: white;
        margin-top: 20px;
        height: 70vh;
        width: 40vw;
        position: sticky;
        top: 100px;

        h2 {
            color: rgba(0, 0, 0, 0.7);
            border-bottom: 1px solid lightgray;
            padding: 15px;
            text-align: center;
        }

        div {
            display: flex;
            padding: 20px;

            span:nth-child(2) {
                margin-left: auto;
            }
        }

        div:last-of-type {
            border-top: 2px dotted lightgray;
            border-bottom: 2px dotted lightgray;
            font-weight: bold;

        }

        p {
            color: green;
            text-align: center;
            padding: 10px;
        }

        div:nth-child(3) span:nth-child(2),
        .summary div:nth-child(4) span:nth-child(2) {
            color: green;
        }

    }

    .saved h2,
    .cart-items-wrapper h2 {
        color: rgba(0, 0, 0, 0.7);
        border-bottom: 1px solid lightgray;
        padding: 15px;
        background-color: white;

    }

    .desc {
        margin-bottom: auto;
        height: 20px;

        p {
            color: gray;
        }

        button {
            margin-right: 20px;

        }

        .price {
            margin: 20px 0 30px 0;
            
            span:first-child {
                text-decoration: line-through;
                color: red;
                font-size: 13px;
                margin-top: 3px;
            }

            span:nth-child(3) {
                color: green;
            }
        }

    }
}





