.newsletter{
    height: 250px;
    width: 100vw;
    background-color: black;
    color: white;
    display: flex;
    flex-direction: column;
    line-height: 3rem;
    justify-content: center;
    align-items: center;
    h2{
        font-size: 2rem;
        font-weight: lighter;

    }
    p{
        font-size:0.9rem;
        font-weight: lighter;

    }
    div{
        display: flex;
        justify-content: center;
        input{
            width: 40vw;
            height: 50px;
            border-radius: 30px 0 0 30px;
            padding-left: 50px;
            font-size: 1.2rem;
            outline: none;
        }
        button{
            width: 10vw;
            height: 50px;
            border-radius:  0 30px 30px 0;
            background-color: #6d2658;
            color: white;
            text-align: center;
            text-transform: uppercase;
            font-size: 1.2rem;
            letter-spacing: 2px;

        }
    }
}