.notify{
    // background-image: url('./assets/images/footer_top_bg.webp');
    background-color: #6d2658;
    position: sticky;
    z-index: 999;
    top: 0;
    height: 30px;
    font-size: 0.9rem;
    color: white;
    align-items: center;
    display: flex;
    justify-content: center;
    padding: 0 5vw;
    p{
        margin-left: auto;
    }
    svg{
        font-size: 20px;
        margin-left: auto;
        cursor: pointer;
        &:hover{
            color: #34132a;
        }
    }
}
.hide{
    display: none;
}