.cardContianer {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;

    .card {
        width: 220px;
        height: 330px;
        padding: 10px;
        border: 1px solid lightgray;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        gap: 5px;

        img {
            height: 200px;
            width: 200px;
            object-fit: cover;

            &:hover {
                object-fit: contain;

            }

        }

        h2 {
            font-size: 1rem;
            color: rgba(0, 0, 0, 0.7);

        }
        .review {
            display: flex;
            gap: 5px;
            align-items: center;
    
            div:first-child {
                display: flex;
                align-items: center;
                color: white;
                background-color: green;
                padding: 2px 5px;
                border-radius: 5px;
                height: 25px;
                gap: 5px;
                width: 48px;
    
            }
    
            div:nth-child(3) {
    
                font-size: 0.9rem;
                color: green;
            }
        }
    
        .price {
            display: flex;
            gap: 5px;
            align-items: center;
    
            span:first-child {
                text-decoration: line-through;
                color: red;
                font-size: 13px;
                margin-top: 3px;
            }
    
            a {
                background-color: yellowgreen;
                color: white;
                display: flex;
                align-items: center;
                padding: 4px;
                font-size: 14px;
                border-radius: 5px;
                gap: 3px;
                margin-left: auto;
    
                svg {
                    margin-top: 2px;
                    transition: all 0.5s;
                }
    
                &:hover svg {
                    transform: rotateY(360deg);
                    color: black;
                }
    
            }
        }
    }

    

}