@import url("https://fonts.googleapis.com/css2?family=Rock+Salt&display=swap");
.cardContianer {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}
.cardContianer .card {
  width: 220px;
  height: 330px;
  padding: 10px;
  border: 1px solid lightgray;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.cardContianer .card img {
  height: 200px;
  width: 200px;
  -o-object-fit: cover;
     object-fit: cover;
}
.cardContianer .card img:hover {
  -o-object-fit: contain;
     object-fit: contain;
}
.cardContianer .card h2 {
  font-size: 1rem;
  color: rgba(0, 0, 0, 0.7);
}
.cardContianer .card .review {
  display: flex;
  gap: 5px;
  align-items: center;
}
.cardContianer .card .review div:first-child {
  display: flex;
  align-items: center;
  color: white;
  background-color: green;
  padding: 2px 5px;
  border-radius: 5px;
  height: 25px;
  gap: 5px;
  width: 48px;
}
.cardContianer .card .review div:nth-child(3) {
  font-size: 0.9rem;
  color: green;
}
.cardContianer .card .price {
  display: flex;
  gap: 5px;
  align-items: center;
}
.cardContianer .card .price span:first-child {
  text-decoration: line-through;
  color: red;
  font-size: 13px;
  margin-top: 3px;
}
.cardContianer .card .price a {
  background-color: yellowgreen;
  color: white;
  display: flex;
  align-items: center;
  padding: 4px;
  font-size: 14px;
  border-radius: 5px;
  gap: 3px;
  margin-left: auto;
}
.cardContianer .card .price a svg {
  margin-top: 2px;
  transition: all 0.5s;
}
.cardContianer .card .price a:hover svg {
  transform: rotateY(360deg);
  color: black;
}

nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  background-color: black;
  padding: 0 5vw;
  height: 80px;
  overflow: hidden;
}
nav:hover {
  background-color: white;
}
nav:hover .logo p,
nav:hover ul li a {
  color: black;
}
nav:hover .logo p span,
nav:hover ul li a span {
  color: #fff;
}
nav:hover ul a svg,
nav:hover .icons ul li:first-child a span {
  color: rgba(0, 0, 0, 0.5);
}
nav .logo p {
  color: white;
  font-size: 1.7rem;
  font-family: "Rock Salt", cursive;
}
nav ul {
  display: flex;
  list-style: none;
  gap: 5px;
}
nav ul li {
  position: relative;
  cursor: pointer;
}
nav ul li:hover a {
  color: #e46d30;
}
nav ul li:hover a svg {
  color: #e46d30;
}
nav ul li:hover a span {
  color: #fff;
}
nav ul li a {
  padding: 50px 0;
  color: white;
  font-size: 12px;
  letter-spacing: 2px;
  text-transform: uppercase;
}
nav ul li a svg {
  font-size: 24px;
  color: white;
  margin-top: 12px;
}
nav .cart-icon {
  position: relative;
  margin-right: 5px;
}
nav .cart-icon span {
  position: absolute;
  top: 3px;
  right: -5px;
  background-color: orangered;
  height: 18px;
  width: 18px;
  border-radius: 50%;
  text-align: center;
  padding-left: 1px;
}
nav .navlinks ul {
  gap: 30px;
}
nav .navlinks ul li:hover a:after {
  content: "";
  position: absolute;
  height: 5px;
  width: 5px;
  border-radius: 50%;
  background-color: #e46d30;
  transform: translateX(-50%);
  left: 50%;
  bottom: -12px;
}

.notify {
  background-color: #6d2658;
  position: -webkit-sticky;
  position: sticky;
  z-index: 999;
  top: 0;
  height: 30px;
  font-size: 0.9rem;
  color: white;
  align-items: center;
  display: flex;
  justify-content: center;
  padding: 0 5vw;
}
.notify p {
  margin-left: auto;
}
.notify svg {
  font-size: 20px;
  margin-left: auto;
  cursor: pointer;
}
.notify svg:hover {
  color: #34132a;
}

.hide {
  display: none;
}

.Slider {
  width: 100vw;
  position: relative;
  overflow: hidden;
}
.Slider img {
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.slick-dots {
  bottom: 20px;
}
.slick-dots li button {
  width: 2em;
  height: 2em;
  border: 1px solid #fff;
  border-radius: 50%;
}
.slick-dots li button:before {
  position: absolute;
  top: 29%;
  left: 29%;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  content: "";
  opacity: 0.5;
  background-color: #fff;
  transform: translate(-50%, -50%);
  transition: all 0.3s ease-in-out;
}
.slick-dots li button:hover button {
  border-color: tomato;
}
.slick-dots li button:hover button::before {
  background-color: tomato;
  opacity: 1;
}
.slick-dots li.slick-active button {
  border-color: tomato;
}
.slick-dots li.slick-active button:before {
  background-color: tomato;
  opacity: 1;
}

.slick-prev, .slick-next {
  width: 150px;
  height: 300px;
  /* background-color:  red; */
  position: absolute;
  top: 20%;
  transform: translateX(-50%);
  z-index: 10;
}

.slick-prev:before, .slick-next:before {
  color: #000;
  font-size: 1.5rem;
}

.slick-prev:hover, .slick-next:hover, .slick-prev:focus, .slick-next:focus {
  outline: none;
}

.slick-next {
  right: -175px;
}

.slick-prev {
  left: 100px;
}

@media screen and (max-width: 576px) {
  .slick-dots {
    bottom: 10px;
  }
  .slick-prev, .slick-next {
    width: 120px;
    height: 150px;
  }
  .slick-next {
    right: -150px;
  }
  .slick-prev {
    left: 75px;
  }
  .slick-prev:before, .slick-next:before {
    font-size: 0.7rem;
  }
}
@media screen and (max-width: 796px) {
  .slick-prev, .slick-next {
    top: 0;
  }
  .slick-prev:before, .slick-next:before {
    font-size: 1rem;
  }
}
