@import url('https://fonts.googleapis.com/css2?family=Rock+Salt&display=swap');


nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    background-color: black;
    padding: 0 5vw;
    height: 80px;
    overflow: hidden;
    &:hover {
        background-color: white;

        .logo p,
        ul li a {
            color: black;
            span{
                color: #fff;
            }
        }

        ul a svg,
        .icons ul li:first-child a span {
            color: rgba(0, 0, 0, 0.5);
        }
    }

    .logo p {
        color: white;
        font-size: 1.7rem;
        font-family: 'Rock Salt', cursive;
    }

    ul {
        display: flex;
        list-style: none;
        gap: 5px;
       
        li {
            position: relative;
            cursor: pointer;

            &:hover {
                a {
                    color: #e46d30;

                    svg {
                        color: #e46d30;
                    }
                    span{
                        color: #fff;
                    }
                }
            }

            a {
                padding: 50px 0;
                color: white;
                font-size: 12px;
                letter-spacing: 2px;
                text-transform: uppercase;

                svg {
                    font-size: 24px;
                    color: white;
                    margin-top: 12px;

                }
            }
        }
    }

    .cart-icon {
        position: relative;
        margin-right: 5px;
        span {
            position: absolute;
            top: 3px;
            right: -5px;
            background-color: orangered;
            height: 18px;
            width: 18px;
            border-radius: 50%;
            text-align: center;
            padding-left: 1px;
        }
    }

    .navlinks {
        ul {
            gap: 30px;
            li {
                &:hover {
                    a {
                        &:after {
                            content: "";
                            position: absolute;
                            height: 5px;
                            width: 5px;
                            border-radius: 50%;
                            background-color: #e46d30;
                            transform: translateX(-50%);
                            left: 50%;
                            bottom: -12px;
                        }
                    }

                }
            }
        }
    }

}