.bestNewArrivals{
    padding: 2vw 5vw;
    .wrapper{
        .title{
            display: flex;
            justify-content: center;
            flex-direction: column;
            gap: 15px;
            align-items: center;
            h3{
                color: rgba(52, 19, 42, 0.7);
                font-size: 1.2rem;
                text-transform: uppercase;
            }
            button{
                border: 1px solid rgba(52, 19, 42, 0.4);
                padding: 8px 25px;
                font-size: 0.8rem;
                letter-spacing: 5px;
                color: rgba(52, 19, 42, 0.6);
                text-transform: uppercase;
                width: 100px;
                text-align: center;
                cursor: pointer;
                &:hover{
                border: 1px solid rgba(52, 19, 42, 1);
                color: rgba(52, 19, 42, 0.8);

                }

            }
        }
        .cards{
            padding: 15px;
            display: flex;
            justify-content: center;
            align-items: center;
            .settings{
                width: 75vw;
                position: relative;
                overflow: hidden;
                .cardsWrapper{
                    cursor: pointer;
                    font-size: .75rem;
                    text-align: center;
                    height: 300 !important;
                    width: 200px !important;
                    &:hover .card button{
                        display: block;
                    }
                    .card{
                        position: relative;
                        margin-bottom: 15px;
                        img{
                            height: 300px;
                            width: 200px;
                        }
                        button{
                            background-color: #f58b76;
                            position: absolute;
                            left: 50%;
                            width: 80%;
                            padding: 5px;
                            border-radius: 2px;
                            transform: translateX(-50%);
                            bottom: 8px;
                            font-weight: lighter;
                            display: none;
                        }
                        svg{
                            position: absolute;
                            right: 8px;
                            top: 8px;
                            color: #f58b76;
                            font-size: 1.2rem;
                            &:hover{
                                color: white;
                                stroke: #f58b76;
                            }
                        }
                    }
                    div{
                        p{
                            color: #7f7373;
                        }
                        
                    }
                }
                .slick-next{
                    right: -200px;
                    top:5%;
                }
                .slick-prev{
                    left: -70px;
                    top:5%;
                    z-index: 100;
                    text-align: end;

                }
                
            }
        }
    }
}