.footer{
    background-color: #34132a;
    display: flex;
    padding: 1vw 5vw;
    gap: 40px;
    ul{
        display: flex;
        list-style-type: none;
        flex-direction: column;
        li:nth-child(1){
            margin-bottom: 1rem;
            font-weight: bold;
            font-size: 0.9rem;
             color: white;
            cursor:auto;

             &:hover{
                text-decoration: none;

             }

        }
        li{
            color: rgba(256, 256, 256, 0.5);
            font-size: 0.8rem;
            padding: 3px;
            cursor: pointer;
            &:hover{
                    text-decoration: underline;
            }
        }
        .logo{
        font-family: 'Rock Salt', cursive;

        }
    }
    
div:last-child{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 4vw;
}
    
}
.footer-bottom{
    background-color: #26081d;
    color: rgba(256, 256, 256, 0.8);
    text-align: center;
    height: 50px;
    line-height: 50px;

    font-size: 0.9rem;
}
